<template>
  <div>
    <el-row style="text-align:center;margin:40px">
      <h2></h2>
    </el-row>
    <el-row style="text-align:center;margin-top:100px">
      <el-upload
        drag
        :before-upload="beforeAvatarUpload"
        :limit="1"
        :on-success="handleUploadSuccess"
        :on-error="handleUploadError"
        name="attachmentFile"
        :data="{auditTaskId:auditTaskId,companyId:companyId,auditFirmId:auditFirmId}"
        :action="uploadApi">
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">docx</div>
      </el-upload>
    </el-row>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data(){
    return{
      uploadApi:'http://miyo-tech.iask.in:10012/business/auditFirm/auditTaskData/uploadAuditTaskAttachment'
    }
  },
  computed: {
    ...mapGetters(['auditFirmId','auditTaskId','companyId'])
  },
  methods:{
    beforeAvatarUpload(file){
      const isDocx = file.type==='application/vnd.openxmlformats-officedocument.wordprocessingml.document'
      if(!isDocx){
        this.$message.warning('docx')
      }
      return isDocx
    },
    handleUploadError() {
      this.$message.error('')
    },
    handleUploadSuccess() {
      this.$message.success('')
    }
  }
}
</script>

